:root {
  --background: #0B0F14;
  --foreground: #FFFFFF;
  --accent: #A3E635;
  --muted: #9CA3AF;
}

/* Custom Tactical Backgrounds */
.bg-grid {
  background-size: 40px 40px;
  background-image:
    linear-gradient(to right, rgba(255, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
}

.bg-radial-fade {
  background: radial-gradient(circle at center, transparent 0%, #0B0F14 100%);
}

.topo-line {
  pointer-events: none;
  position: absolute;
  border-radius: 9999px;
  border-width: 0.5px;
  border-color: rgb(255 255 255 / 0.05);
}

/* Animations */
@keyframes drift {
  from {
    transform: rotate(0deg) scale(1);
  }

  to {
    transform: rotate(360deg) scale(1.1);
  }
}

.animate-drift {
  animation: drift 60s linear infinite;
}

@keyframes pulse-coordinate {
  0% {
    opacity: 0.2;
    transform: scale(1);
  }

  50% {
    opacity: 0.5;
    transform: scale(1.05);
  }

  100% {
    opacity: 0.2;
    transform: scale(1);
  }
}

.pulse-node {
  animation: pulse-coordinate 4s ease-in-out infinite;
}

